@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
.template-button {
    border: none;
    padding: none;
    width: 10vw;
    height: 30px;
    margin: 0 .5vw;
    border-radius: 3px;
    font-size: 1vw;
    cursor: pointer;

}

.template-button:hover {
    background-color: white;
}

.template-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}


@media only screen and (max-width: 1000px) {

    .template-container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
.template-input {
    margin: 0;
    padding: 0;
    border: none;
    grid-row-start: 1;
    grid-row-end: 2;
    border-radius: 0;
    outline: 0;
    width: 100%;
    text-align: center;
    border-image: linear-gradient(180deg, #fff 20%, #aaa 20%, #aaa 80%, #fff 80%) 1 / 1px;
    font-size: clamp(5px, calc(5px + 16 * (100vw - 400px) / 1100), 21px);
}

.template-input:focus,
input:focus {
    outline: 0;
}
* {
    box-sizing: border-box;
}

:root,
* {
    font-size: 16px;
}

.outer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80vw;
}

.template-label {
    width: 20%;
    height: 30px;
    margin: 10px 0;
    text-align: center;
    outline: 0;
    border: 1px solid black;
}

.template-label::-webkit-input-placeholder {
    color: rgb(0, 0, 0, .3);
    text-align: center;
    opacity: 1;
}

.template-label:-ms-input-placeholder {
    color: rgb(0, 0, 0, .3);
    text-align: center;
    opacity: 1;
}

.template-label::placeholder {
    color: rgb(0, 0, 0, .3);
    text-align: center;
    opacity: 1;
}

.code-container {
    height: 99px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(3, 1fr);
    background-color: white;
    border: 1px solid black;
}

[class|=code-template] {
    grid-column: 1 / 2;
    display: grid;
    grid-template-rows: repeat(1, 1fr);
}

[class|=code-template] span {
    grid-row: 1 / 2;
}

.code-template-top {
    grid-row: 1 / 2;
}

.code-template-top span {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    font-size: clamp(5px, calc(5px + 11 * (100vw - 400px) / 1100), 16px);
}

.code-template-middle {
    grid-row: 2 / 3;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
}

.code-template-middle span {
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 1px solid black;
    font-size: clamp(5px, calc(5px + 11 * (100vw - 400px) / 1100), 16px);
}

.code-template-middle span:nth-child(1) {
    border-left: none;
}

.code-template-bottom {
    grid-row: 3 / 4;
}

@media only screen and (max-width: 1000px) {
    .outer {
        flex-direction: column;
        width: 99vw;
        justify-content: center;
    }
}

.download-button {
    margin-left: .5vw;
    border: none;
    padding: none;
    width: 50px;
    height: 50px;
    border-radius: 100vw;
    cursor: pointer;
    margin-top: 74.5px;
}

.download-button:hover .download-svg {
    width: 70%;
}

.download-svg {
    width: 60%;
}


@media only screen and (max-width: 1000px) {
    .download-button {
        margin-top: 10px;
    }
}
.more-button {
    width: 100px;
    height: 100px;
    border-radius: 100vh;
    border: 4px solid black;
    background-image: url(/static/media/plusIcon.f7e2f749.svg);
    background-repeat: no-repeat;
    background-position: 50% ;
    background-size: 50%;
    cursor: pointer;
}

.more-button:hover {
    background-size: 60%;
}

.more-button-container {
    display: flex;
    flex-direction: row;
}
main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1vh;
  gap: 1vh;
  margin-top: 23px;
}

body {
  background-color: wheat;
  font-family: 'Roboto Mono', monospace;
}

h1 {
  font-size: 1.6rem
}
